export interface OptimizePromptParams {
  roughPrompt: string;
  targetAI: string;
  promptStyle: string;
  systemPrompt: string;
}

export async function optimizePrompt(params: OptimizePromptParams): Promise<string> {
  const { roughPrompt, targetAI, promptStyle, systemPrompt } = params;
  const apiKey = process.env.NEXT_PUBLIC_OPENROUTER_API_KEY;

  if (!apiKey) {
    throw new Error('API key is missing. Please set NEXT_PUBLIC_OPENROUTER_API_KEY in your environment variables.');
  }

  const endpoint = 'https://openrouter.ai/api/v1/chat/completions';
  const model = 'openai/gpt-4o'; // Default multimodal, multilingual model

  // Constructing the messages as per the multimodal structured guidelines:
  const messages = [
    {
      role: 'system',
      content: [
        { type: 'text', text: systemPrompt }
      ]
    },
    {
      role: 'user',
      content: [
        { 
          type: 'text', 
          text: `Optimize the following prompt using the 4-D methodology (Deconstruct, Diagnose, Develop, Deliver).

Target AI: ${targetAI}
Prompt Style: ${promptStyle}

Apply the appropriate optimization techniques:
- For Creative tasks: Multi-perspective + tone emphasis
- For Technical tasks: Constraint-based + precision focus
- For Educational tasks: Few-shot examples + clear structure
- For Complex tasks: Chain-of-thought + systematic frameworks

Raw Prompt: ${roughPrompt}

Please provide the optimized prompt following the specified format for ${promptStyle} mode.` 
        }
      ]
    }
  ];

  const payload = {
    model,
    messages,
    temperature: 0.7,
    max_tokens: 2000
  };

  try {
    const res = await fetch(endpoint, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${apiKey}`,
        'HTTP-Referer': typeof window !== 'undefined' ? window.location.origin : 'http://localhost:8000',
        'X-Title': 'Lyra AI Prompt Optimizer'
      },
      body: JSON.stringify(payload),
    });

    if (!res.ok) {
      const errorText = await res.text();
      throw new Error(`Server responded with ${res.status}: ${errorText}`);
    }

    const data = await res.json();
    
    // Extract the optimized prompt from the API response
    if (data.choices && data.choices[0] && data.choices[0].message) {
      return data.choices[0].message.content;
    } else {
      throw new Error('Unexpected API response format');
    }
  } catch (err) {
    console.error('Error optimizing prompt:', err);
    throw err;
  }
}
