'use client';

import React, { useState } from 'react';
import { optimizePrompt } from '@/lib/ai';
import OptimizedPrompt from './OptimizedPrompt';

const DEFAULT_SYSTEM_PROMPT = `You are Lyra, a master-level AI prompt optimization specialist. Your mission: transform any user input into precision-crafted prompts that unlock AI's full potential across all platforms.

THE 4-D METHODOLOGY
1. DECONSTRUCT
Extract core intent, key entities, and context
Identify output requirements and constraints
Map what's provided vs. what's missing

2. DIAGNOSE
Audit for clarity gaps and ambiguity
Check specificity and completeness
Assess structure and complexity needs

3. DEVELOP
Select optimal techniques based on request type:
Creative â†’ Multi-perspective + tone emphasis
Technical â†’ Constraint-based + precision focus
Educational â†’ Few-shot examples + clear structure
Complex â†’ Chain-of-thought + systematic frameworks
Assign appropriate AI role/expertise
Enhance context and implement logical structure

4. DELIVER
Construct optimized prompt
Format based on complexity
Provide implementation guidance

OPTIMIZATION TECHNIQUES
Foundation: Role assignment, context layering, output specs, task decomposition
Advanced: Chain-of-thought, few-shot learning, multi-perspective analysis, constraint optimization

Platform Notes:
ChatGPT/GPT-4: Structured sections, conversation starters
Claude: Longer context, reasoning frameworks
Gemini: Creative tasks, comparative analysis
Others: Apply universal best practices`;

const LyraForm = () => {
  const [roughPrompt, setRoughPrompt] = useState('');
  const [targetAI, setTargetAI] = useState('ChatGPT');
  const [promptStyle, setPromptStyle] = useState('DETAIL');
  const [systemPrompt, setSystemPrompt] = useState(DEFAULT_SYSTEM_PROMPT);
  const [optimized, setOptimized] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [showSystemPrompt, setShowSystemPrompt] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    if (!roughPrompt.trim()) {
      setError('Please enter a prompt to optimize.');
      return;
    }
    setLoading(true);
    setOptimized('');
    
    try {
      const result = await optimizePrompt({ roughPrompt, targetAI, promptStyle, systemPrompt });
      setOptimized(result);
    } catch (err: any) {
      console.error(err);
      setError(err.message || 'Failed to optimize the prompt. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="w-full max-w-4xl mx-auto">
      {/* Welcome Message */}
      <div className="mb-8 p-6 bg-card border border-border rounded-lg">
        <div className="text-center mb-4">
          <h2 className="text-2xl font-bold text-foreground mb-2">Hello! I'm Lyra, your AI prompt optimizer.</h2>
          <p className="text-muted-foreground">I transform vague requests into precise, effective prompts that deliver better results.</p>
        </div>
        
        <div className="space-y-3 text-sm">
          <div>
            <p className="font-semibold text-foreground mb-2">What I need to know:</p>
            <ul className="list-disc list-inside space-y-1 text-muted-foreground ml-4">
              <li><strong>Target AI:</strong> ChatGPT, Claude, Gemini, or Other</li>
              <li><strong>Prompt Style:</strong> DETAIL (I'll ask clarifying questions first) or BASIC (quick optimization)</li>
            </ul>
          </div>
          
          <div>
            <p className="font-semibold text-foreground mb-2">Examples:</p>
            <ul className="list-disc list-inside space-y-1 text-muted-foreground ml-4">
              <li>"DETAIL using ChatGPT â€” Write me a marketing email"</li>
              <li>"BASIC using Claude â€” Help with my resume"</li>
            </ul>
          </div>
          
          <p className="text-muted-foreground italic">Just share your rough prompt and I'll handle the optimization!</p>
        </div>
      </div>

      {/* Main Form */}
      <div className="bg-card border border-border rounded-lg p-6 shadow-sm">
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Rough Prompt Input */}
          <div>
            <label className="block text-sm font-medium text-foreground mb-2">
              Your Rough Prompt *
            </label>
            <textarea
              value={roughPrompt}
              onChange={(e) => setRoughPrompt(e.target.value)}
              className="w-full p-3 border border-input rounded-md bg-background text-foreground placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:border-transparent resize-none"
              rows={4}
              placeholder="Enter your rough prompt here... (e.g., 'Write me a marketing email' or 'Help with my resume')"
            />
          </div>

          {/* Target AI and Prompt Style */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">
                Target AI Platform
              </label>
              <select 
                value={targetAI} 
                onChange={(e) => setTargetAI(e.target.value)} 
                className="w-full p-3 border border-input rounded-md bg-background text-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:border-transparent"
              >
                <option value="ChatGPT">ChatGPT</option>
                <option value="Claude">Claude</option>
                <option value="Gemini">Gemini</option>
                <option value="Other">Other</option>
              </select>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">
                Optimization Style
              </label>
              <select 
                value={promptStyle} 
                onChange={(e) => setPromptStyle(e.target.value)}
                className="w-full p-3 border border-input rounded-md bg-background text-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:border-transparent"
              >
                <option value="DETAIL">DETAIL (comprehensive optimization)</option>
                <option value="BASIC">BASIC (quick optimization)</option>
              </select>
            </div>
          </div>

          {/* System Prompt Section */}
          <div>
            <div className="flex items-center justify-between mb-2">
              <label className="text-sm font-medium text-foreground">
                System Prompt (Advanced)
              </label>
              <button
                type="button"
                onClick={() => setShowSystemPrompt(!showSystemPrompt)}
                className="text-xs text-muted-foreground hover:text-foreground transition-colors"
              >
                {showSystemPrompt ? 'Hide' : 'Show & Edit'}
              </button>
            </div>
            
            {showSystemPrompt && (
              <textarea
                value={systemPrompt}
                onChange={(e) => setSystemPrompt(e.target.value)}
                className="w-full p-3 border border-input rounded-md bg-background text-foreground placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:border-transparent resize-none text-xs"
                rows={8}
                placeholder="Customize the system prompt that guides Lyra's optimization process..."
              />
            )}
          </div>

          {/* Submit Button */}
          <button
            type="submit"
            className="w-full p-3 bg-primary text-primary-foreground rounded-md hover:bg-primary/90 transition-colors duration-200 font-medium disabled:opacity-50 disabled:cursor-not-allowed"
            disabled={loading}
          >
            {loading ? (
              <div className="flex items-center justify-center space-x-2">
                <div className="w-4 h-4 border-2 border-primary-foreground/30 border-t-primary-foreground rounded-full animate-spin"></div>
                <span>Optimizing your prompt...</span>
              </div>
            ) : (
              'Optimize Prompt'
            )}
          </button>
          
          {/* Error Message */}
          {error && (
            <div className="p-3 bg-destructive/10 border border-destructive/20 rounded-md">
              <p className="text-destructive text-sm">{error}</p>
            </div>
          )}
        </form>
      </div>

      {/* Optimized Result */}
      {optimized && <OptimizedPrompt prompt={optimized} />}
    </div>
  );
};

export default LyraForm;
