import React, { useState } from 'react';

interface OptimizedPromptProps {
  prompt: string;
}

const OptimizedPrompt: React.FC<OptimizedPromptProps> = ({ prompt }) => {
  const [copied, setCopied] = useState(false);

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(prompt);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error('Copy failed', err);
    }
  };

  return (
    <div className="mt-8 p-6 bg-card border border-border rounded-lg shadow-sm">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-xl font-semibold text-foreground">Your Optimized Prompt</h2>
        <button 
          onClick={handleCopy} 
          className="px-4 py-2 bg-primary text-primary-foreground rounded-md hover:bg-primary/90 transition-colors duration-200 text-sm font-medium"
        >
          {copied ? 'Copied!' : 'Copy to Clipboard'}
        </button>
      </div>
      
      <div className="bg-muted/50 rounded-md p-4 border">
        <pre className="text-sm text-foreground whitespace-pre-wrap font-mono leading-relaxed">
          {prompt}
        </pre>
      </div>
      
      <div className="mt-4 text-xs text-muted-foreground">
        <p>ðŸ’¡ <strong>Pro Tip:</strong> Copy this optimized prompt and paste it into your preferred AI platform for better results.</p>
      </div>
    </div>
  );
};

export default OptimizedPrompt;
